@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;0,700;1,400;1,700&display=swap);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"Open Sans",sans-serif}
.App{text-align:center}a{text-decoration:none;color:unset}
.home-page .content-wrapper{padding-left:249px}
.blank-layout{background:linear-gradient(87deg, #5e72e4 0, #825ee4 100%) !important;min-height:100vh;width:100%}.blank-layout .second-bg{background:#152746;width:100%;min-height:40vh;position:absolute;bottom:0;right:0;left:0}


.sidebar::-webkit-scrollbar{width:3px;background-color:#f5f5f5}.sidebar{height:100vh;background-color:#fff;max-width:100%;width:249px;overflow-y:scroll;z-index:1}.sidebar-header{height:78px}.sidebar-body{padding:0px 1.5rem}.sidebar-body .sidebar-nav{list-style:none;padding-left:0px;margin:0}.sidebar-body::before{content:"";margin:1rem;display:block}.sidebar__logo{display:flex;height:72px;align-items:center;justify-content:center}.sidebar__logo .logo-img{height:40px;object-fit:cover}.sidebar-heading{margin:0;margin-bottom:8px;text-transform:uppercase;color:#8898aa;font-size:12px;font-weight:600}.sidebar__item{margin-top:2px}.sidebar__item-inner{padding:.856rem 0rem;display:flex;align-items:center;transition:color .3s ease 0s;font-weight:400;line-height:21px}.sidebar__item-inner-icon{font-size:1rem;margin-right:10px;color:#f5365c;min-width:1.5rem}.sidebar__item-inner span{font-family:"Open Sans",sans-serif;text-transform:capitalize;color:#555;font-size:14px;line-height:21px;font-weight:400}.sidebar__item #icon-purple{color:#5e72e4;line-height:21px}.sidebar .lines{margin:16px 0px}
.btn{font-size:.875rem;font-weight:600;line-height:1.5;padding:.625rem 1.25rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center;border-radius:.25rem;color:#fff;border:1px solid transparent;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08)}.btn--green{background-color:#2dce89;border:solid 1px #2dce89}.btn--gray{background-color:#adb5bd;border:solid 1px #adb5bd}.btn--paleblue{background-color:#5e72e4;border:solid 1px #5e72e4}.btn--black{background-color:#212529;border:solid 1px #212529}.btn:hover{-webkit-transform:translateY(-1px);transform:translateY(-1px);box-shadow:0 7px 14px rgba(50,50,93,.1),0 3px 6px rgba(0,0,0,.08)}
.table{width:100%;border-collapse:collapse;color:#525f7f}.table .table-head th{font-size:10px;font-weight:600;padding:10px;letter-spacing:1px;text-align:inherit;background:#f6f9fc;text-transform:uppercase;border-top:2px solid #e9ecef;white-space:nowrap;color:#8898aa}.table .table-body td{border-top:1px solid #e9ecef;text-align:inherit;padding:10px;white-space:nowrap}
.beadcrumb{display:flex;align-items:center}.beadcrumb__title{text-transform:capitalize;font-size:20px;color:#fff}.beadcrumb .links{display:flex;align-items:center;justify-content:flex-start;list-style-type:none;background-color:#e9ecef;padding:10px;margin:20px;border-radius:7px}.beadcrumb .links__home{position:relative;margin:auto 15px}.beadcrumb .links__home--icon{font-size:17px;font-weight:900;color:#5e72e4}.beadcrumb .links__home--icon:hover{color:blue}.beadcrumb .links__item::before{position:absolute;content:"/";font-size:16px;margin-left:-10px}.beadcrumb .links__item{text-transform:capitalize;font-size:18px;color:#8898aa}
.bt{display:flex}.bt_style{margin-left:15px}.bt_style-info{color:#fff;background-color:#11cdef;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08);font-size:.999rem;font-weight:600;line-height:1.5;padding:.62rem 1.25rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center;border-radius:.25rem;border:1px solid transparent;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08)}.bt_style-info:hover{-webkit-transform:translateY(-1px);transform:translateY(-1px);box-shadow:0 7px 14px rgba(50,50,93,.1),0 3px 6px rgba(0,0,0,.08)}.bt_style-edit{color:#fff;background-color:#5e72e4;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08);font-size:.999rem;font-weight:600;line-height:1.5;padding:.62rem 1.25rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center;border-radius:.25rem;border:1px solid transparent;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08)}.bt_style-edit:hover{-webkit-transform:translateY(-1px);transform:translateY(-1px);box-shadow:0 7px 14px rgba(50,50,93,.1),0 3px 6px rgba(0,0,0,.08)}.bt_style-delete{color:#fff;background-color:#f5365c;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08);font-size:.999rem;font-weight:600;line-height:1.5;padding:.62rem 1.25rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center;border-radius:.25rem;border:1px solid transparent;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08)}.bt_style-delete:hover{-webkit-transform:translateY(-1px);transform:translateY(-1px);box-shadow:0 7px 14px rgba(50,50,93,.1),0 3px 6px rgba(0,0,0,.08)}
.bt{display:flex}.bt_feature{margin-left:15px}.bt_feature-search{color:blue;background-color:#fff;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08);font-size:.999rem;font-weight:500;line-height:1.5;padding:.62rem 1.25rem;text-align:center;border-radius:.25rem;border:1px solid blue;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08)}.bt_feature-search:hover{background-color:blue;color:#fff}.bt_feature-retype{color:#212529;background-color:#fff;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08);font-size:.999rem;font-weight:500;line-height:1.5;padding:.62rem 1.25rem;text-align:center;border-radius:.25rem;border:1px solid blue;box-shadow:0 4px 6px rgba(50,50,93,.11),0 1px 3px rgba(0,0,0,.08)}.bt_feature-retype:hover{background-color:#212529;color:#fff}
.main-layout{min-height:100vh;font-family:Open Sans,sans-serif;font-size:1rem;font-weight:400;line-height:1.5;margin:0;text-align:left;color:#525f7f;background-color:#f8f9fe;display:flex;position:relative}.main-layout__content{width:100%;min-height:100vh;z-index:1}.main-layout__bg{background-color:#5e72e4;width:100%;height:35vh;position:absolute;z-index:0}
