@import "../../../assets/scss/variable";
.bt{
    display: flex;
    &_feature{
            margin-left: 15px;
            &-search{
                color: $color-blue;
                background-color: white;
                box-shadow: 0 4px 6px rgba(50, 50, 93, .11), 0 1px 3px rgba(0, 0, 0, .08);
                font-size: .999rem;
                font-weight: 500;
                line-height: 1.5;
                padding: .620rem 1.25rem;
                text-align: center;
                border-radius: .25rem;
                border: 1px solid $color-blue;
                box-shadow: 0 4px 6px rgb(50 50 93 / 11%), 0 1px 3px rgb(0 0 0 / 8%);
                
            }
            &-search:hover{
                background-color: $color-blue;
                color: white;
            }
            &-retype{
                color: $color-black;
                background-color: white;
                box-shadow: 0 4px 6px rgba(50, 50, 93, .11), 0 1px 3px rgba(0, 0, 0, .08);
                font-size: .999rem;
                font-weight: 500;
                line-height: 1.5;
                padding: .620rem 1.25rem;
                text-align: center;
                border-radius: .25rem;
                border: 1px solid $color-blue;
                box-shadow: 0 4px 6px rgb(50 50 93 / 11%), 0 1px 3px rgb(0 0 0 / 8%);
            }
            &-retype:hover{
                background-color: $color-black;
                color: white;
            }
            
            
            

    }
    
}

